import * as React from "react";
import Layout from "../../components/layout";
import HeroAnimation from "../../components/home/heroAnimation";
import vivaVideo from "../../images/VIVA.mp4";
import ProductDesc from "../../components/product/productDesc";
import ProductTable from "../../components/product/productTable";
import vivaImgP1 from "../../images/viva/viva_p1.png";
import vivaImgP2 from "../../images/viva/viva_p2.png";
import vivaImgP3 from "../../images/viva/viva_p3.png";
import vivaImgP4 from "../../images/viva/viva_p4.png";
import ProductBrandSection from "../../components/product/productBrandSection";
import { projectsData } from "../../data/projectsData";
import ProductHero from "../../components/product/productHero";

const product = projectsData.viva;

const VivaPage = () => {
  return (
    <Layout pageTitle="Viva">
      <ProductHero mediaSrc={vivaVideo} isImg={false} elem={product} />
      <div className="row">
        <div className="col">
          <img
            alt="Viva"
            src={vivaImgP1}
            width={"100%"}
            className="d-inline-block align-text-top"
          />
        </div>
      </div>
      <div className="row">
        <ProductBrandSection imgSrc={vivaImgP2} brand={product.brand} />
      </div>
      <div className="row">
        <ProductBrandSection imgSrc={vivaImgP3} brand={product.visualId} />
      </div>
      <div className="row">
        <div className="col">
          <img
            alt="Viva"
            src={vivaImgP4}
            width={"100%"}
            height={"250px"}
            className="d-inline-block align-text-top"
          />
        </div>
      </div>
    </Layout>
  );
};

export default VivaPage;

export const Head = () => <title>Lane7 - VIVA</title>;
